import keyMirror from 'keymirror';

const Modes = keyMirror({
    BRUSH: null,
    ERASER: null,
    LINE: null,
    SELECT: null,
    RESHAPE: null,
    PEN: null,
    PENTWO: null,
    OVAL: null,
    RECT: null,
    ROUNDED_RECT: null
});

export default Modes;



